@import '../../style/variables.scss';

.card__wrapper {
    padding-bottom: 30px;
    background-color: transparentize($lightColor, .6);
    border-radius: 10px;
    .cards__header {
        padding: 20px 5px 20px;
        border-radius: 5px 5px 0 0;
        text-align: center;
        font-weight: 400;
        font-size: 18px;
    }
    .cards__list {
        padding: 0 20px;
        height: 63vh;
        @include templateFlex(flex-start, center);
        flex-direction: column;
        row-gap: 10px;
        overflow-y: auto;
        word-wrap: break-word;
        border-radius: 10px;
        .card {
            width: 290px;
            padding: 10px;
            border-radius: 5px;
            background-color: $lightColor;
            box-shadow:  0 2px 3px $lightBlue, 0 0 4px $mainColor;
            @include templateTransition(background-color);
            cursor: pointer;
            &:hover {
                background-color: transparentize($lightColor, .3);
            }
            &:active {
                background-color: transparentize($lightColor, 0.2);
                box-shadow: 0px 4px 6px 3px #2d53db1a;
            }
            span {
                display: block;
                max-width: 80px;
                margin-left: auto;
                font-style: italic;
            }
        }
    }
};

.long__template {
    color: #df6d6d;
};